	module.exports = {"data":{"markdownRemark":{"html":"<h2>“<a href=\"/filez/music/21.mp3\">TRZ-MASTER 2001” を聞く</a> (2:13 | [filesize]<a href=\"https://danmaq.com/filez/music/21.mp3%5B/filesize%5D\">https://danmaq.com/filez/music/21.mp3[/filesize]</a>)</h2>\n<p> 初めてクラブでトランス聞く数日前に、自分の頭の中で持っていたトランスのイメージを描き出してみた。これを聞くと、今まで自分がいかにトランスというものを誤解していたか良く分かる。ちなみに数日後制作した “<a href=\"/underground-trance\">UnderGround Trance</a>” はクラブで本当のトランスと言うものに触れた後の自分の中のイメージ。  昔クラスタにうｐされてた BMS で、曲名忘れたけど周囲より一足先に (当時は BMS でトランスと言ったらみんなサイケばっか作ってた) 今風のユーロトランスを作った方がいて、そのメロディに感動してなんとか自分流にアレンジ出来ないものか、と四苦八苦していたものの毎日が暇人だった、古きよき時代。</p>","frontmatter":{"cover":null,"date":"2001-12-23T00:00:00+09:00","strDate":"2001/12/22","title":"TRZ-MASTER 2001"}}},"pathContext":{"path":"/ja/blog/2001/12/23-trz-master2001/","langKey":"ja"}}

